import React, { useState } from "react"

// import Offcanvas from 'react-bootstrap/Offcanvas';
import Nav from 'react-bootstrap/Nav';
import NavDropdown from 'react-bootstrap/NavDropdown';
// import { FaBars } from "react-icons/fa";

import './Navbar.scss'

import siteData from '../../api/services'

class NavigationBar extends React.Component {
    state = {
        itemsMenu: [],
        itemsServices: []
    }
    async componentDidMount() {
        const data = await siteData.getIndexInfo()
        console.log(data)
        await this.setState({ 
            itemsMenu: data[0].Header.Menu,
            itemsServices: data[0].Header.Services,
        })
    }
    // Componente do NavBar
    render () {
        return (
            <div style={{ width: "100%" }}>
                <div className="navbar">
                    {/* <div className="logo"><img width="100px" src={Logo} alt='logo' /></div> */}
                    {/* <FaBars onClick={handleShow}></FaBars>

                    <Offcanvas show={show} onHide={handleClose}>
                        <Offcanvas.Header closeButton>
                        <Offcanvas.Title>Menu</Offcanvas.Title>
                        </Offcanvas.Header>
                        <Offcanvas.Body style={{ padding: '0px'}}>
                        <div style={{ display: 'flex', flexDirection: 'column'}}>
                            { itemsMenu.map( item => (
                                <Link style={{ textDecoration: 'none', color: '#939393', borderBottom: '1px solid', padding: '5px'}} to={`/${item.link}`}>{item.title}</Link>
                            ))
                            }
                        </div>
                        </Offcanvas.Body>
                    </Offcanvas> */}
                    <div style={{ display: 'flex'}}>
                        { 
                            this.state.itemsMenu.map( item => (
                                <Nav.Link key={item.id} href={`/${item.content}`}>{item.name}</Nav.Link>
                            ))
                        }
                    </div>
                    <NavDropdown title="Servios" id="basic-nav-dropdown">
                        {
                            this.state.itemsServices.map( item => (
                                <NavDropdown.Item key={item.id} style={{color: 'black'}} href={`http://localhost:8000${item.content}`}>{item.name}</NavDropdown.Item>
                            ))
                        }
                        <NavDropdown.Divider />
                        <NavDropdown.Item style={{color: 'black'}} href="/services">Ver Todos Servios</NavDropdown.Item>
                    </NavDropdown>
                </div>
            </div>
        )
    }
}

export default NavigationBar